<template>
  <div class="message-alert">
    <div class="alert alert-dismissible"
      :class="'alert-' + item.status"
      v-for="(item, i) in messages" :key="i">
      {{ item.message }}
      <button type="button" class="close" @click="removeMessage(i)" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
  </div>
</template>

<style scope>
  .message-alert {
    position: fixed;
    max-width: 50%;
    top: 56px;
    right: 20px;
    z-index: 1100;
  }
</style>

<script>
export default {
  name: 'Navbar',
  methods: {
    updateMessage (message, status) {
      this.$store.dispatch('alertModules/updateMessage', { message, status })
    },
    removeMessage (num) {
      // this.messages.splice(num, 1)
      this.$store.dispatch('alertModules/removeMessage', num)
    },
    removeMessageWithTiming (timestamp) {
      // const vm = this
      // setTimeout(() => {
      //   vm.messages.forEach((item, i) => {
      //     if (item.timestamp === timestamp) {
      //       vm.messages.splice(i, 1)
      //     }
      //   })
      // }, 5000)
      this.$store.dispatch('alertModules/removeMessageWithTiming', timestamp)
    }
  },
  computed: {
    messages () {
      return this.$store.state.alertModules.messages
    }
  },
  created () {
    // const vm = this
    // vm.$bus.$on('message:push', (message, status = 'warning') => {
    //   vm.updateMessage(message, status)
    // })
    // 自定義名稱 'messsage:push'
    // message: 傳入參數
    // status: 樣式，預設值為 warning
  }
}
</script>
